import RegisterForm from '@/components/auth/register-form'
import LoginForm from '@/components/auth/login-form'
import OauthForm from '@/components/auth/oauth-form'
import { auth } from '@/auth'
import { redirect } from 'next/navigation';
import { CirclePlus, LogIn, Globe, UserPlus, AlertTriangle } from 'lucide-react'

const errors = new Map();
// ... (mantener mismo mapeo de errores)

async function PaginaLogin({ searchParams }) {
  const { error, callbackUrl } = await searchParams
  globalThis.callbackUrl = callbackUrl

  const sesion = await auth()
  if (sesion) redirect('/dashboard')

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md bg-white rounded-2xl shadow-xl overflow-hidden">
        {/* Tabs */}
        <div className="flex border-b border-gray-200">
          <input
            id="signin"
            type="radio"
            name="sign"
            className="hidden peer/login"
            defaultChecked={true}
          />
          <label
            htmlFor="signin"
            className="flex-1 flex flex-col items-center p-4 cursor-pointer transition-all 
                       peer-checked/login:bg-red-50 peer-checked/login:text-red-600 text-gray-500"
          >
            <LogIn className="h-5 w-5 mb-2" />
            <span className="text-sm font-medium">Iniciar Sesión</span>
          </label>

          <input
            id="signup"
            type="radio"
            name="sign"
            className="hidden peer/register"
          />
          <label
            htmlFor="signup"
            className="flex-1 flex flex-col items-center p-4 cursor-pointer transition-all 
                       peer-checked/register:bg-red-50 peer-checked/register:text-red-600 text-gray-500"
          >
            <UserPlus className="h-5 w-5 mb-2" />
            <span className="text-sm font-medium">Registro</span>
          </label>

          <input
            id="signoauth"
            type="radio"
            name="sign"
            className="hidden peer/oauth"
          />
          <label
            htmlFor="signoauth"
            className="flex-1 flex flex-col items-center p-4 cursor-pointer transition-all 
                       peer-checked/oauth:bg-red-50 peer-checked/oauth:text-red-600 text-gray-500"
          >
            <Globe className="h-5 w-5 mb-2" />
            <span className="text-sm font-medium">Social</span>
          </label>
        </div>

        {/* Forms Container */}
        <div className="p-8">
          {error && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center gap-3">
              <AlertTriangle className="h-5 w-5 text-red-600" />
              <p className="text-red-600 text-sm">{errors.get(error)}</p>
            </div>
          )}

          <RegisterForm className="hidden peer-checked/register:block animate-fade-in" />
          <LoginForm className="hidden peer-checked/login:block animate-fade-in" />
          <OauthForm className="hidden peer-checked/oauth:block animate-fade-in" />
        </div>

        {/* Gradient Border Effect */}
        <div className="h-1 bg-gradient-to-r from-red-600 to-orange-500 opacity-10" />
      </div>
    </div>
  )
}

export default PaginaLogin